import { Inject, Injectable } from '@angular/core';
import {
  ActivatedRoute,
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterStateSnapshot
} from '@angular/router';
import { NavigatorProvider, ORIGIN_FLOW, OriginFlow } from '@nequi/features/commons';
import { DataTreatmentInteractor, NoDataException } from '@nequi/features/data-treatment/core';
import { LoadingService } from '@nequi/ui/services';

import { InformationConfig } from '../pages/information/information.config';

@Injectable()
export class UserDataMiddleware implements CanActivate {
  private phoneNumber: string;
  private fromDeepLink: boolean;

  constructor(
    @Inject(ORIGIN_FLOW) private originFlow: OriginFlow,
    private dataTreatmentInteractor: DataTreatmentInteractor,
    private navigatorProvider: NavigatorProvider,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private loadingService: LoadingService
  ) {}

  public async canActivate(
    _route: ActivatedRouteSnapshot,
    _state: RouterStateSnapshot
  ): Promise<boolean> {
    try {
      const state = this.router.getCurrentNavigation().extras.state;

      this.phoneNumber = state?.phoneNumber;
      this.fromDeepLink = state?.fromDeepLink;

      if (this.originFlow === OriginFlow.PROFILE || this.originFlow === OriginFlow.BACKPACK) {
        await this.loadingService.show();

        const data = await this.dataTreatmentInteractor.getUserData();

        this.router.getCurrentNavigation().extras.state = { ...state, data: data };

        await this.loadingService.hide();
      }

      return true;
    } catch (error) {
      if (error.constructor.name === NoDataException.name) {
        this.navigatorProvider.navigateForward(
          [InformationConfig.routes.profileInformation],
          {
            state: { phoneNumber: this.phoneNumber, fromDeepLink: this.fromDeepLink, data: null },
            relativeTo: this.activatedRoute,
          }
        );
      }

      await this.loadingService.hide();

      return false;
    }
  }
}
