import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot } from '@angular/router';
import { Color } from '@nequi/features/card/core';
import { NavigatorProvider, TranslateProvider } from '@nequi/features/commons';
import { DataTreatmentConfig } from '@nequi/features/data-treatment/config';
import { CardModalService, LoadingService } from '@nequi/ui/services';

import { InformationConfig } from '../pages/information/information.config';

@Injectable()
export class InformationMiddleware implements CanActivate {
  constructor(
    private translateProvider: TranslateProvider,
    private cardModalService: CardModalService,
    private navigatorProvider: NavigatorProvider,
    private loadingService: LoadingService
  ) {}

  public async canActivate(
    _route: ActivatedRouteSnapshot,
    _state: RouterStateSnapshot
  ): Promise<boolean> {
    await this.loadingService.hideWithoutFeedback();

    if (_route.queryParams?.fromDeepLink) {
      await this.showCardModal();

      return false;
    }

    return true;
  }

  private async showCardModal() {
    await this.translateProvider.loadModule(DataTreatmentConfig.i18n.moduleKey);
    const messages = this.translateProvider.get(InformationConfig.i18n.cardModalLock);

    this.cardModalService.show({
      title: messages.title,
      content: messages.content,
      imgPath: messages.image,
      buttons: [
        {
          text: messages.buttons.accept,
          handler: () => {
            this.navigatorProvider.navigateForward([InformationConfig.routes.information], {
              state: { fromDeepLink: true },
            });
          },
        },
        {
          text: messages.buttons.cancel,
          color: Color.MEDIANOCHE,
        },
      ],
    });
  }
}
