import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import {
  FeatureModuleConfig,
  ModuleConfigProvider,
  ORIGIN_FLOW,
  OriginFlow
} from '@nequi/features/commons';
import {
  DataTreatmentInteractor,
  DataTreatmentRepository
} from '@nequi/features/data-treatment/core';
import { NequiUiModule } from '@nequi/ui';
import { TranslateModule } from '@ngx-translate/core';

import { DataTreatmentConfig } from '../config';
import { FeaturesDataTreatmentRoutingModule } from './features-data-treatment-routing.module';
import { InformationMiddleware } from './middleware/information.middleware';
import { UserDataMiddleware } from './middleware/user-data.middleware';
import { AuthorizationPage } from './pages/authorization/authorization.page';
import { FormPage } from './pages/form/form.page';
import { InformationPage } from './pages/information/information.page';

@NgModule({
  declarations: [FormPage, InformationPage, AuthorizationPage],
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    ReactiveFormsModule,
    NequiUiModule,
    FeaturesDataTreatmentRoutingModule,
    TranslateModule,
  ],
  providers: [
    {
      provide: DataTreatmentInteractor,
      deps: [DataTreatmentRepository],
      useClass: DataTreatmentInteractor,
    },
    {
      provide: ModuleConfigProvider,
      useValue: DataTreatmentConfig,
    },
    InformationMiddleware,
    UserDataMiddleware,
  ],
})
export class FeaturesDataTreatmentModule {
  public static withConfig(
    config: FeatureModuleConfig
  ): ModuleWithProviders<FeaturesDataTreatmentModule> {
    return {
      ngModule: FeaturesDataTreatmentModule,
      providers: [{ provide: ORIGIN_FLOW, useValue: OriginFlow.ENROLL }, ...config],
    };
  }
}
