import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { IonContent } from '@ionic/angular';
import { Attribute, ParameterInteractor } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';

import { AuthorizationConfig } from './authorization.config';

@Component({
  selector: 'app-authorization',
  templateUrl: './authorization.page.html',
  styleUrls: ['./authorization.page.scss'],
})
export class AuthorizationPage extends FeatureBasePage implements OnInit {
  @ViewChild(IonContent) public content: IonContent;

  public isScrollEnd = false;
  public termsAndConditions: string;
  public config = AuthorizationConfig;
  public busyIndicator = {
    show: false,
    error: '',
  };

  private phoneNumber: string;
  private fromDeepLink: boolean;

  constructor(
    protected readonly injector: Injector,
    private parameterInteractor: ParameterInteractor,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) {
    super(injector);
    this.phoneNumber = this.router.getCurrentNavigation().extras.state?.phoneNumber;
    this.fromDeepLink = this.router.getCurrentNavigation().extras.state?.fromDeepLink;
  }

  public async ngOnInit() {
    await this.getTermsContent();
  }

  public acceptAgreement() {
    this.goToForm();
  }

  public scrollToBottom() {
    this.content.scrollToBottom();
  }

  public async handleScroll() {
    const element = await this.content.getScrollElement();

    this.isScrollEnd =
      Math.abs(element.scrollHeight - element.clientHeight - element.scrollTop) < 1;
  }

  public async getTermsContent() {
    try {
      this.busyIndicator.show = true;
      this.termsAndConditions = (
        await this.parameterInteractor.getParameter<Attribute[]>(AuthorizationConfig.parameters.id)
      )
        .filter(
          (attribute: Attribute) => attribute.value === AuthorizationConfig.parameters.termsKey
        )
        .map((param) => param.code)
        .reduce((param) => param);

      this.busyIndicator.show = false;
    } catch (error) {
      this.busyIndicator.error = error;
    }
  }

  private goToForm() {
    this.navigatorProvider.navigateForward([this.config.routes.form], {
      state: { phoneNumber: this.phoneNumber, fromDeepLink: this.fromDeepLink },
      relativeTo: this.activatedRoute,
    });
  }
}
