import { Component, Inject, Injector, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ORIGIN_FLOW, OriginFlow } from '@nequi/features/commons';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { InformationConfig } from './information.config';

@Component({
  selector: 'app-information',
  templateUrl: './information.page.html',
  styleUrls: ['./information.page.scss'],
})
export class InformationPage extends FeatureBasePage implements OnInit {
  public config = InformationConfig;
  public information: Array<Record<string, string>> = [];
  private phoneNumber: string;
  private fromDeepLink: boolean;

  constructor(
    injector: Injector,
    private translateProvider: TranslateProvider,
    private analyticsInteractor: AnalyticsInteractor,
    private router: Router,
    @Inject(ORIGIN_FLOW) private originFlow: OriginFlow,
    private activatedRoute: ActivatedRoute
  ) {
    super(injector);

    this.phoneNumber = this.router.getCurrentNavigation().extras.state?.phoneNumber;
    this.fromDeepLink = this.router.getCurrentNavigation().extras.state?.fromDeepLink;
  }

  public get showGoBack() {
    return this.originFlow !== OriginFlow.BACKPACK || this.fromDeepLink;
  }

  public ngOnInit() {
    this.loadContent();
  }

  public nextPage() {
    if (this.originFlow === OriginFlow.BACKPACK) {
      this.sendAnalyticsIntentionDataTreatment();
    }

    this.navigatorProvider.navigateForward([this.config.routes.authorization], {
      state: { phoneNumber: this.phoneNumber, fromDeepLink: this.fromDeepLink },
      relativeTo: this.activatedRoute,
    });
  }

  private loadContent() {
    let i18nKey = this.config.i18n.content;

    if (this.originFlow === OriginFlow.BACKPACK || this.originFlow === OriginFlow.PROFILE) {
      i18nKey = this.config.i18n.contentLock;
    }

    this.information = this.translateProvider.get(i18nKey);
  }

  private sendAnalyticsIntentionDataTreatment() {
    const analytics = this.config.analytics.intentionDataTreatment;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: analytics.name,
        eventProps: stringsArrayToObjectWithValues(analytics.params, [getFormattedDateTimes()]),
      },
    });
  }
}
