import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { I18nModuleResolver } from '@nequi/features/commons';

import { DataTreatmentConfig } from '../config';
import { InformationMiddleware } from './middleware/information.middleware';
import { UserDataMiddleware } from './middleware/user-data.middleware';
import { AuthorizationPage } from './pages/authorization/authorization.page';
import { FormPage } from './pages/form/form.page';
import { InformationPage } from './pages/information/information.page';

const routes: Routes = [
  {
    path: '',
    component: InformationPage,
    canActivate: [InformationMiddleware],
  },
  {
    path: 'information',
    component: InformationPage,
  },
  {
    path: 'authorization',
    component: AuthorizationPage,
  },
  {
    path: 'form-data',
    component: FormPage,
    canActivate: [UserDataMiddleware],
  },
  {
    path: 'form',
    component: FormPage,
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(I18nModuleResolver.setup(routes, [DataTreatmentConfig.i18n.moduleKey])),
  ],
  exports: [RouterModule],
})
export class FeaturesDataTreatmentRoutingModule {}
