import { FormControl, FormGroup } from '@angular/forms';
import {
  DataTreatmentEntity,
  DataTreatmentItem,
  DataTreatmentOption,
  OperationType
} from '@nequi/features/data-treatment/core';

export interface CheckBoxControl extends DataTreatmentOption {
  control: FormControl;
}

export class FormViewModel {
  public form: FormGroup;
  public controls: CheckBoxControl[] = [];
  public busyIndicator = {
    show: false,
    error: '',
  };

  public buildCheckboxes(items: DataTreatmentItem[]) {
    this.form = new FormGroup({});

    items.forEach((item) => {
      this.createCheckbox({
        answer: true,
        id: item.id,
        text: item.text,
      });
    });
  }

  public updateCheckboxValues(data: DataTreatmentEntity) {
    if (data?.items.length > 0) {
      data.items.forEach((ele: DataTreatmentOption) => {
        if (this.form.get(ele.id)) {
          this.form.get(ele.id).setValue(ele.answer);
        } else {
          this.createCheckbox(ele);
        }
      });
    }
  }

  public mapOptionValues() {
    this.controls.forEach((control: CheckBoxControl) => {
      control.answer = this.form.get(control.id).value;
    });
  }

  public buildRequest(operationType?: OperationType): DataTreatmentEntity {
    return {
      items: this.controls.map((control) => {
        return {
          id: control.id,
          answer: control.answer,
          text: control.text,
        };
      }),
      flow: operationType || OperationType.CREATE,
    };
  }

  private createCheckbox(item: DataTreatmentOption) {
    const checkboxControl = new FormControl(item.answer, []);

    this.controls.push({
      id: item.id,
      text: item.text,
      answer: item.answer,
      control: checkboxControl,
    });

    this.form.addControl(item.id, checkboxControl);
  }
}
