import { Component, Inject, Injector, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { ORIGIN_FLOW, OriginFlow } from '@nequi/features/commons';
import {
  AnalyticsInteractor,
  AnalyticsType,
  Attribute,
  ParameterInteractor
} from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import {
  DataTreatmentEntity,
  DataTreatmentInteractor,
  NoDataException,
  OperationType
} from '@nequi/features/data-treatment/core';
import { RegistryLiteModel } from '@nequi/features/lite-registry/data';
import { LoadingService, ToastService } from '@nequi/ui';
import { CardModalService } from '@nequi/ui/services';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { FormConfig } from './form.config';
import { FormViewModel } from './form.view-model';

@Component({
  selector: 'app-form',
  templateUrl: './form.page.html',
  styleUrls: ['./form.page.scss'],
})
export class FormPage extends FeatureBasePage implements OnInit {
  public viewModel = new FormViewModel();
  private phoneNumber: string;
  private operationType: OperationType = OperationType.UPDATE;
  private fromDeepLink: boolean;
  private data: DataTreatmentEntity;

  constructor(
    injector: Injector,
    @Inject(ORIGIN_FLOW) public originFlow: OriginFlow,
    private dataTreatmentInteractor: DataTreatmentInteractor,
    private parameterInteractor: ParameterInteractor,
    private loadingService: LoadingService,
    private toastService: ToastService,
    private router: Router,
    private registryLiteModel: RegistryLiteModel,
    private analyticsInteractor: AnalyticsInteractor,
    private applicationDataInteractor: ApplicationDataInteractor,
    private translateProvider: TranslateProvider,
    private cardModalService: CardModalService
  ) {
    super(injector);

    const state = this.router.getCurrentNavigation().extras.state;

    this.phoneNumber = state?.phoneNumber;
    this.fromDeepLink = state?.fromDeepLink;
    this.data = state?.data;
  }

  public async ngOnInit() {
    await this.getData();
  }

  public get showDisclaimer() {
    return this.originFlow === OriginFlow.ENROLL || this.originFlow === OriginFlow.BACKPACK;
  }

  public async getData() {
    try {
      this.viewModel.busyIndicator.show = true;
      this.viewModel.busyIndicator.error = '';

      const url = await this.getOptionsUrl();
      const options = await this.dataTreatmentInteractor.getOptions(url);

      this.viewModel.buildCheckboxes(options);

      if (this.originFlow === OriginFlow.PROFILE) {
        this.setProfileData();
      }

      if (this.originFlow === OriginFlow.BACKPACK) {
        this.setData();
      }

      this.viewModel.busyIndicator.show = false;
    } catch (error) {
      this.viewModel.busyIndicator.error = this.translateProvider.get(FormConfig.i18n.errorMessage);
    }
  }

  public async setData() {
    try {
      const data = await this.dataTreatmentInteractor.getUserData();

      this.viewModel.updateCheckboxValues(data);
      this.viewModel.busyIndicator.show = false;
    } catch (error) {
      if (error.constructor.name === NoDataException.name) {
        this.operationType = OperationType.CREATE;
        this.viewModel.busyIndicator.show = false;

        return;
      }

      this.viewModel.busyIndicator.error = error.message;
    }
  }

  public saveOptions() {
    this.viewModel.mapOptionValues();

    if (this.originFlow === OriginFlow.ENROLL) {
      this.setTemporalDataTreatment();

      return;
    }

    this.saveDataTreatment();
  }

  private async getOptionsUrl(): Promise<string> {
    return (await this.parameterInteractor.getParameter<Attribute[]>(FormConfig.parameters.id))
      .filter((attribute: Attribute) => attribute.value === FormConfig.parameters.urlKey)
      .map((param) => param.code)
      .reduce((param) => param);
  }

  private async saveDataTreatment() {
    try {
      await this.loadingService.show();
      const shouldUnlock = this.originFlow === OriginFlow.BACKPACK && !this.fromDeepLink;

      await this.dataTreatmentInteractor.saveUserData(
        this.viewModel.buildRequest(this.operationType),
        shouldUnlock
      );

      await this.loadingService.hide(true);
      this.successNavigation();
    } catch (error) {
      await this.loadingService.hideWithoutFeedback();
      this.toastService.showError(error.message);
    }
  }

  private successNavigation() {
    const url = this.getSuccessUrl();

    const navigation = {
      [OriginFlow.ENROLL]: () => this.enrollNavigation(url),
      [OriginFlow.PROFILE]: () => this.profileNavigation(url),
      [OriginFlow.BACKPACK]: () => this.backpackNavigation(url),
    };

    if (navigation[this.originFlow]) {
      return navigation[this.originFlow]();
    }

    this.navigatorProvider.navigateForward(url);
  }

  private profileNavigation(url: string) {
    this.sendAnalyticsAcceptDataTreatment();
    this.navigatorProvider.navigateBack(url);
  }

  private enrollNavigation(url: string) {
    this.navigatorProvider.navigateForward(url, {
      state: {
        phoneNumber: this.phoneNumber,
      },
    });
  }

  private getSuccessUrl() {
    return {
      [OriginFlow.ENROLL]: FormConfig.routes.token,
      [OriginFlow.PROFILE]: FormConfig.routes.dashboard,
      [OriginFlow.BACKPACK]: FormConfig.routes.dashboard,
    }[this.originFlow];
  }

  private async setTemporalDataTreatment() {
    this.registryLiteModel.dataTreatment = JSON.stringify(this.viewModel.buildRequest());
    await this.applicationDataInteractor.save(
      FormConfig.data.userData,
      this.registryLiteModel.dataTreatment
    );

    this.successNavigation();
  }

  private sendAnalyticsAcceptDataTreatment() {
    const analytics = FormConfig.analytics.acceptDataTreatment;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: analytics.name,
        eventProps: stringsArrayToObjectWithValues(analytics.params, [getFormattedDateTimes()]),
      },
    });
  }

  private async backpackNavigation(url: string) {
    this.navigatorProvider.navigateRoot(url);

    const message = this.translateProvider.get(FormConfig.i18n.allReady);

    await this.cardModalService.show({
      title: message.title,
      imgPath: message.image,
      content: message.content,
      buttons: [
        {
          text: message.buttons.ready,
        },
      ],
    });
  }

  private setProfileData() {
    if (this.data === null) {
      this.operationType = OperationType.CREATE;

      return;
    }

    this.viewModel.updateCheckboxValues(this.data);
  }
}
